
import { useState } from "react";
import React from "react";

const BondCalc = () => {
    const [faceValue, setF] = useState();
    const [years, setN] = useState();
    const [Rate, setC] = useState();
    const [bondPrice, setP] = useState();
    const [mvalue, setResult] = useState();
    const [YTM, setYTM] = useState();

    const calculate = () => {
        // A=p(1+(r/n))^(nt)
        // I= C*F
        const result = (faceValue * Rate/100);
        const Price = (bondPrice * Math.pow((1 + (Rate/100 / (years * 100))), (years)));
        const A = ((faceValue - bondPrice)/(years));
        const B = ((faceValue + bondPrice)/2);

        setResult(result.toFixed(2));
        setP(Price.toFixed(4));
        setYTM(B);
    };

    return (
        <div className="row">
            <div className="col-lg-8">
                <form>
                    <div className="mb-3 row">
                        <label for="exampleFormControlInput1" className=" col-sm-4 calc-label">Face value in â‚¹</label>
                        <div className="col-sm-8">
                            <input onChange={(e) => setF(e.target.value)} type="text" className="calc-control" id="exampleFormControlInput1" placeholder="" />
                        </div>
                    </div>
                    <div className="mb-3 row">
                        <label for="exampleFormControlInput1" className="col-sm-4 calc-label">Coupon Rate in %</label>
                        <div className="col-sm-8">
                            <input onChange={(e) => setC(e.target.value)} type="number" className="calc-control" id="exampleFormControlInput1" placeholder="" />
                        </div>
                    </div>
                    <div className="mb-3 row">
                        <label for="exampleFormControlInput1" className=" col-sm-4 calc-label">Years to Maturity</label>
                        <div className="col-sm-8">
                            <input onChange={(e) => setN(e.target.value)} type="number" className="calc-control" id="exampleFormControlInput1" placeholder="" />
                        </div>
                    </div>
                    <div className="mb-3 row">
                        <label for="exampleFormControlInput1" className=" col-sm-4 calc-label">Current Bond Price</label>
                        <div className="col-sm-8">
                            <input onChange={(e) => setP(e.target.value)} type="number" className="calc-control" id="exampleFormControlInput1" placeholder="" />
                        </div>
                    </div>
                    <button onClick={() => { calculate(); }} type="button" className="btn btn-primary">Calculate</button>
                </form>
            </div>
            <div className="col-lg-4">
                <div className="pricipalDiv">
                    <h3>Details</h3>
                    <h4>Maturity Value: {mvalue}</h4>
                    <h4>Rate of Interest: {bondPrice}%</h4>
                    <h4>YTM: {YTM}</h4>

                </div>
            </div>
        </div>

    )
}

export default BondCalc